<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Employees</h4>
      </li>
      <li v-for="employee in employees" :key="employee.id" class="collection-item">
        <div class="chip">{{employee.department}}</div>

        {{employee.employee_id}} : {{employee.name}}
        <router-link
          class="secondary-content"
          v-bind:to="{name: 'ViewEmployee', params: {employee_id: employee.employee_id}}"
        >
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>

    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";

export default {
  name: "Dashboard",
  data() {
    return {
      employees: []
    };
  },
  created() {
    db.collection("employees")
      .orderBy("department")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const { employee_id, name, department, position } = doc.data();
          const data = {
            id: doc.id,
            employee_id: employee_id,
            name: name,
            department: department,
            position: position
          };
          this.employees.push(data);
        });
      });
  }
};
</script>

